
import XInput from 'vux/src/components/x-input/index.vue'
import XTextarea from 'vux/src/components/x-textarea/index.vue'
import XSwitch from 'vux/src/components/x-switch/index.vue'
import XAddress from 'vux/src/components/x-address/index.vue'
import ChinaAddressV4Data from 'vux/src/datas/china_address_v4.json'
import Group from 'vux/src/components/group/index.vue'

import { setTimeout } from 'timers';
export default {
components:{
XInput,
Group,
XSwitch,
XAddress,
ChinaAddressV4Data,
XTextarea
},
data(){
return {
consignee:'',
phone:'',
address:'',
area:'',
isDefault:false,
addressData:ChinaAddressV4Data,
}
},
methods:{

addAddress(){
this.$vux.loading.show({
text:'正在保存'
})
setTimeout(()=>{
this.$vux.loading.hide()
this.$vux.toast.text("添加成功",'center')
},1000)
setTimeout(()=>{
this.$router.go(-1)
},2000)
}
}
}



