import Vue from 'vue'
import Router from 'vue-router'
import home from '../views/home'
import cart from '../views/cart'
import personal from '../views/personal'
import detail from '../views/detail'
import createIndent from '../views/create-indent'
import addresslist from '../views/address-list'
import editAddress from '../views/edit-address'
import indent from '../views/indent'
Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'home',
      component: home,
      meta: {
        title: '商城'
      }
    },
    {
      path: '/cart',
      name: 'cart',
      component: cart,
      meta: {
        title: '购物车'
      }
    },
    {
      path: '/personal',
      name: 'personal',
      component: personal,
      meta: {
        title: '我的'
      }
    },
    {
      path: '/detail',
      name: 'detail',
      component: detail,
      meta: {
        title: ''
      }
    },
    {
      path: '/createIndent',
      name: 'createIndent',
      component: createIndent,
      meta: {
        title: '确认订单'
      }
    },
    {
      path: '/addresslist',
      name: 'addresslist',
      component: addresslist,
      meta: {
        title: '地址管理'
      }
    },
    {
      path: '/editaddress',
      name: 'editAddress',
      component: editAddress,
      meta: {
        title: '编辑地址'
      }
    },
    {
      path:"/indent",
      name:"indent",
      component:indent,
      meta:{
        title:'订单中心'
      }
    }
  ]
})



