
import Tab from 'vux/src/components/tab/tab.vue'
import TabItem from 'vux/src/components/tab/tab-item.vue'
import Swiper from 'vux/src/components/swiper/swiper.vue'
import CellBox from 'vux/src/components/cell-box/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Badge from 'vux/src/components/badge/index.vue'
import Spinner from 'vux/src/components/spinner/index.vue'
import XAddress from 'vux/src/components/x-address/index.vue'
import ChinaAddressV4Data from 'vux/src/datas/china_address_v4.json'
import XButton from 'vux/src/components/x-button/index.vue'
import value2name from 'vux/src/filters/value2name.js'
import XNumber from 'vux/src/components/x-number/index.vue'


import {mapState,mapMutations} from 'vuex'
export default {
data() {
return {
srcIp: '',
proid: '', //  商品ID
tabindex: 0,
loop: true,
autoSwiper: true,
swiperList: [

{
url: 'javascript',
img:
'http://img.hb.aicdn.com/3f82e919081b223b03bb61d534695ddf4b06d9db11d138-asTcz7_fw658',
title: '今日生鲜不打烊'
},
{
url: 'javascript',
img:
'http://img.hb.aicdn.com/f9d5f1512debfb121131d10a9784f238363eb3e4e9d1c-jYYhoz_fw658',
title: '生鲜数据图'
}
],
detail: {

title: '生鲜一条龙',
detail: '支持在线寄送，店家宰杀清洗，到店自取，更多优惠，尽在36℃',
newprice: '100.20',
oldprice: '300.00',
merchant: '36℃精选',
stock: 0 
},
option: '大份生鲜',
realTime: '29:30:56',
addressInfo: ['广东省', '深圳市', '南山区'],
addressIn: {
title: '送至',
addressData: ChinaAddressV4Data
},
title: '默认为空',
title2: '设置值',
addressData: ChinaAddressV4Data,
showAddress: false,
quantity:1,
}
},
components: {
Tab,
TabItem,
Swiper,
CellBox,
Group,
Cell,
Badge,
Spinner,
XAddress,
ChinaAddressV4Data,
XButton,
XNumber
},
computed:{
...mapState('cart',['cartNum'])
},
mounted() {
this.srcIp = this.$route.query.src
this.proid = this.$route.query.proid
console.log(this.srcIp)










},
methods: {
...mapMutations('cart',['setCartNum']),

initPage() {},
switchTabItem(index) {
this.tabindex = index
},
doShowAddress() {
this.showAddress = true
setTimeout(() => {
this.showAddress = false
}, 2000)
},
logHide(str) {
console.log('on-hide', str)
console.log(this.addressInfo)
},
logShow(str) {
console.log('on-show')
},

createIndent(){
this.$router.push({path:'/createIndent',query:{
skuIds:[this.proid]
}})
},
toCart(){
this.$router.push({path:'/cart'})
},

addCart(){
this.$vux.loading.show()

setTimeout(()=>{
this.$vux.loading.hide()
let n = this.cartNum+1
this.setCartNum(n)
this.$vux.toast.show("已添加至购物车")
},1000)
}
}
}



