
import InlineXNumber from 'vux/src/components/inline-x-number/index.vue'

export default {
components: {
InlineXNumber
},
data() {
return {
cartInfo: [
{
id: 0,
img:
'http://img.hb.aicdn.com/1f328be1adcc60404728f890f6c2b80ea506eb9842737-Zf8bpf_fw658',
name:
'把喝杯茶吧憋出病杀虫剂和v成本为救护车v 未激活 救护车救护车存储玩具火车玩具',
price: '100.00',
count: 1
},
{
id: 1,
img:
'http://img.hb.aicdn.com/1f328be1adcc60404728f890f6c2b80ea506eb9842737-Zf8bpf_fw658',
name:
'把喝杯茶吧憋出病杀虫剂和v成本为救护车v 未激活 救护车救护车存储玩具火车玩具',
price: '100.00',
count: 1
}
],
idArr: [],
totalPrice: 0
}
},
computed: {
status() {
if (this.cartInfo.length > 0) {
return true
} else {
return false
}
}
},
created() {},
mounted() {},
methods: {

jSelect(id) {
let f = false
this.idArr.filter(o => {
if (o == id) f = true
})
return f
},

selectSku(card, event) {
let sid = card.id
let f = event.target.checked
let p = Number(card.price)
if (event.target.checked == true) {
this.idArr.push(sid)
this.totalPrice += p
} else {
this.idArr = this.idArr.filter(o => {
return o != sid
})
this.totalPrice -= p
}
},

chooseAll(event) {
this.idArr = []
this.totalPrice = 0
if (event.target.checked) {
this.cartInfo.filter(item => {
this.idArr.push(item.id)
this.totalPrice += Number(item.price)
})
}
},

toCreateIndent() {
if (this.idArr.length < 1) {
this.$vux.toast.show({
text:`请选择商品`,
type:'warn'
})
return
}
this.$router.push({path:'createIndent',query:{
skuIds:this.idArr
}})
}
}
}



