
import { mapState, mapActions, mapMutations } from 'vuex'
import XHeader from 'vux/src/components/x-header/index.vue'
import Actionsheet from 'vux/src/components/actionsheet/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'
import ButtonTab from 'vux/src/components/button-tab/button-tab.vue'
import ButtonTabItem from 'vux/src/components/button-tab/button-tab-item.vue'
import Loading from 'vux/src/components/loading/index.vue'
import Tabbar from 'vux/src/components/tabbar/tabbar.vue'
import TabbarItem from 'vux/src/components/tabbar/tabbar-item.vue'

export default {
directives: {
TransferDom
},
components: {
Loading,
XHeader,
Actionsheet,
ButtonTab,
ButtonTabItem,
Tabbar,
TabbarItem
},
name: 'App',
data() {
return {
showMenus: false,
menus: {
menu1: '关于我们',
},
links:[
{path:'/',replace:true},
{path:'/cart',replace:true},
{path:'/personal',replace:true},
],
}
},
computed: {
...mapState(['loadState', 'loadText']),
...mapState('nav', [
'navBar',
'showBack',
'showMore',
'navTitle',
'tabBar',
'tabIndex'
]),
...mapState('cart', ['cartNum'])
},
mounted() {

this.getUserInfo()
},
methods: {
...mapMutations(['setLoadText']),
...mapMutations('nav', [
'setnavBar',
'setshowBack',
'setshowMore',
'setnavTitle',
'setTabIndex'
]),
...mapMutations('cart', ['setCartNum']),
getUserInfo(){
this.$api.login()
.then(res=>{

})
}
}
}



