




import 'es6-promise/auto'
import Vue from 'vue'
import App from './App'
import router from './router'
import store from './vuex/store'
import config from '../config/index'
import Api from './utils/api'
import fastClick from 'fastclick'
import Check from './utils/check';
import ToastPlugin from 'vux/src/plugins/toast/index.js'
import LoadingPlugin from 'vux/src/plugins/loading/index.js'
import AlertPlugin from 'vux/src/plugins/alert/index.js'
import ConfirmPlugin from 'vux/src/plugins/confirm/index.js'

require('../node_modules/vux/src/styles/reset.less');

fastClick.attach(document.body)
Vue.use(ToastPlugin)
Vue.use(LoadingPlugin)
Vue.use(ConfirmPlugin)
Vue.use(AlertPlugin )


Vue.prototype.$config = process.env.NODE_ENV === 'development'
? config.dev : config.build

Vue.prototype.$check = new Check()


new Api(Vue.prototype.$config, store)



router.beforeEach((to,from,next)=>{
store.commit('nav/setnavTitle',to.meta.title)
document.title = to.meta.title
if(to.path=="/"){
store.commit('nav/setnavBar',false)
store.commit('nav/setTab',true)
store.commit('nav/setTabIndex',0)
}else if(to.path=="/cart"){
store.commit('nav/setnavBar',false)
store.commit('nav/setTab',true)
store.commit('nav/setTabIndex',1)
}else if(to.path=="/personal"){
store.commit('nav/setnavBar',false)
store.commit('nav/setTab',true)
store.commit('nav/setTabIndex',2)
}else {
store.commit('nav/setTab',false)
store.commit('nav/setnavBar',true)
store.commit('nav/setshowBack',true)
store.commit('nav/setshowMore',true)
}
next()
})

Vue.config.productionTip = false

/* eslint-disable no-new */
new Vue({
el: '#app',
router,
store,
template: '<App/>',
components: { App }
})



