
import Cell from 'vux/src/components/cell/index.vue'
import Group from 'vux/src/components/group/index.vue'
import XTextarea from 'vux/src/components/x-textarea/index.vue'

export default {
components: {
Cell,
Group,
XTextarea
},
data() {
return {
idArr:[],

rejectInfo: {
name: '大佬的家',
mobile: '18674087602',
address:
'广东省深圳市福田区某个街道某个地方 某个公司某个公司某个公司某个公司某个公司'
},

skuInfos: [
{
title: '每日优选水果爱的去玩大青蛙地区的气温请我去吃我去玩',
detail: 'asdadqwdqdq使得球队取得轻轻的亲吻',
price: '150.00',
num: 10
}
],

payInfo:{
payPrice:'1500.00'
},

userPonnts:'',
}
},
mounted(){

this.idArr = this.$route.query.skuIds

this.createDefault()
},
methods: {
toAddress() {
this.$router.push({ path: '/addresslist' })
},
createDefault(){

let skuInfos = []
for(let i in this.idArr){
skuInfos.push(this.skuInfos[0])
}
this.skuInfos = skuInfos
}
}
}



