
import Vue from 'vue'
import VueScroller from 'vue-scroller'
import XInput from 'vux/src/components/x-input/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Card from 'vux/src/components/card/index.vue'

import { debug } from 'util'
Vue.use(VueScroller)
export default {
data() {
return {
noData: '',
cardList:[],



















































































searchText: '', //搜索框提示文字
searchValue: '',
clear: true
}
},
components: {
XInput,
Group,
Card
},
mounted() {
this.$refs.myscroller.resize() 
this.$refs.myscroller.finishInfinite(2) 
},
methods: {

refresh() {
setTimeout(() => {
this.$refs.myscroller.finishPullToRefresh()
}, 1500)
},

infinite(done) {
if (!this.noData) {
setTimeout(() => {
this.$refs.myscroller.finishInfinite(2)
})
return
}
let self = this 
let start = this.moveList.length

setTimeout(() => {
for (let i = start + 1; i < start + 10; i++) {
self.cardList.push(i)
}
if (start > 30) {
self.noData = '数据已全部加载完毕'
}
self.$refs.myscroller.resize()
done()
}, 1500)
},

searchPro(e) {
console.log(this.searchValue)
},
toDetail(item) {
this.$router.push({
path: '/detail',
query: {
src: item.srcIp,
proid: item.id
}
})
}
}
}



