export default {
namespaced:true,
   state:{
    navBar:true,    //  是否展示顶栏
    showBack:true,  //  顶栏返回
    showMore:true,  //  顶栏显示更多
    navTitle:'',    //  顶栏标题
    tabBar:true,    //  是否展示底部栏
    tabIndex:0,     //  底部栏标
   },
   mutations:{
       setnavBar(state,data){
        state.navBar = data
       },
       setshowBack(state,data){
        state.showBack = data
       },
       setshowMore(state,data){
        state.showMore = data
       },
       setnavTitle(state,data){
        state.navTitle = data
       },
       setTab(state,data){
           state.tabBar = data
       },
       setTabIndex(state,data){
           state.tabIndex = data
       }
   }
}


